
.list-group{
    max-width: 800px;
    padding: 0px;
    margin: 0 auto;
    margin-top: 3px;
    width: 100%;
    height: 100%;
    
}

.list-group-item{
    
    width: 100%;
    list-style: none;
    padding-left: 10px;
    margin-top: 8px;
    font-size: 25px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;


    
}
